.galCases {
  &__body {
    max-width: 961px;
    margin: 0 auto;
    ul {
      display: grid;
      grid-template-columns: repeat(3, 300px);
      justify-content: center;
      overflow: hidden;
      column-gap: 30px;
    }
    li {
      transform: scaleY(0);
      transform-origin: top;
      min-height: 0px;
      @include transition;
      &._is-active {
        min-height: 350px;
        transform: scaleY(1);
        margin: 0 0 30px 0;
      }
      &:hover {
        & > .galCases__info {
          transform: scaleY(1);
        }
      }
    }
    button {
      svg {
        width: 20px;
        height: 20px;
        fill: black;
        margin: 0 0 0 10px;
        transform: rotate(0deg);
        @include transition;
        &._is-active {
          transform: rotate(720deg);
        }
      }
      padding: 10px 20px;
      border-radius: 5px;
      margin: -10px auto 0;
    }
  }
  &__info {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: gold;
    @include transition;
    transform: scaleY(0);
    transform-origin: top;
    display: flex;
    flex-direction: column;
    padding: 20px 15px;
    h3 {
      color: red;
    }
    a {
      margin: auto 0 0 0;
      cursor: pointer;
    }
    svg {
      width: 20px;
      height: 20px;
      fill: black;
      margin: 0 0 0 10px;
      transform: rotate(0deg);
    }
  }
}
