._accord-hidden-js {
  display: grid;
  grid-template-rows: 0fr;
  overflow: hidden;
  @include transition;

  &._is-active {
    grid-template-rows: 1fr;
  }
}
._accord-hidden-wrap-js {
  min-height: 0;
  min-width: 0;
}

@media (max-width: 767px) {
  ._accord-hidden-js {
    overflow: visible;
  }
}
