.fildset-radio {
  position: relative;
  .form-check {
    position: relative;
  }

  input[type="radio"]:checked,
  input[type="radio"]:not(:checked) {
    position: fixed !important;
    left: -9999px !important;
    height: 0px;
    width: 0px;
  }
  input[type="radio"]:checked + label,
  input[type="radio"]:not(:checked) + label {
    display: inline-block;
    position: relative;
    cursor: pointer;
    opacity: 1;
    line-height: 26px;
    line-height: 26px;
    padding: 0 0 0 46px;
    @include transition;
    &:hover {
      color: lighten(green, 25%);
    }
  }
  input[type="radio"]:checked + label > svg,
  input[type="radio"]:not(:checked) + label > svg {
    display: inline-block !important;
    width: 14px;
    transform: translate(6px, 6px);
    height: 14px;
    position: absolute;
    top: 0;
    left: 0;
    @include transition;
  }

  input[type="radio"]:checked + label:after,
  input[type="radio"]:checked + label::before,
  input[type="radio"]:not(:checked) + label:after,
  input[type="radio"]:not(:checked) + label::before {
    display: block;
    @include transition;
    content: "";
    position: absolute;
    left: 0px;
    top: 0px;
  }

  input[type="radio"]:not(:checked) + label::before {
    border-radius: 50%;
    width: 26px;
    height: 26px;
    border: solid 2.4px #bababa;
    background: transparent;
  }
  input[type="radio"]:checked + label::before {
    border-radius: 50%;
    width: 26px;
    height: 26px;
    background: transparent;
    border: solid 2px green;
  }

  input[type="radio"]:checked + label::after,
  input[type="radio"]:not(:checked) + label::after {
    width: 14px;
    height: 14px;
    border-radius: 100%;
    top: 0;
    left: 0;
    transform: translate(6px, 6px);
    border: solid 2px green;
    background-color: green;
  }
  input[type="radio"]:checked + label::after {
    opacity: 1;
  }
  input[type="radio"]:not(:checked) + label::after {
    opacity: 0;
  }
  &--tema {
    @include flex-aligne-center;
    position: relative;
    width: 105px;
    height: 20px;

    input[type="radio"]:checked + label:after,
    input[type="radio"]:checked + label::before,
    input[type="radio"]:not(:checked) + label:after,
    input[type="radio"]:not(:checked) + label::before {
      display: none;
    }

    input[type="radio"]:checked + label,
    input[type="radio"]:not(:checked) + label {
      width: 54px;
      height: 20px;
      position: relative;
      padding: 0 0 0 0;
      z-index: 10;
    }

    input[type="radio"]:checked + label > svg,
    input[type="radio"]:not(:checked) + label > svg {
      display: inline-block !important;
      width: 20px;
      height: 20px;
      transform: translate(0, 0);
      position: absolute;
    }

    input[type="radio"]#temalight + label > svg {
      right: 0;
      left: auto;
    }

    input[type="radio"]:not(:checked) + label {
      svg {
        fill: $white;
      }
    }
    input[type="radio"]:checked + label {
      svg {
        fill: $periwinkle;
      }
    }
    // input[type="radio"]:checked + label,
    // input[type="radio"]:not(:checked) + label {
    //   height: 20px;
    //   position: relative;
    //   // position: absolute;
    //   // left: 0;
    //   svg {
    //     fill: $periwinkle;
    //     display: inline-block;
    //     opacity: 1;
    //   }
    // }
    // input[type="radio"]:checked + label::before,
    // input[type="radio"]:not(:checked) + label::before {
    //   width: 45px;
    //   height: 20px;
    //   border-radius: 32px;
    //   border: solid 1px $white;
    //   background: transparent;
    // }

    span {
      display: inline-block;
      width: 45px;
      height: 19px;
      border-radius: 32px;
      border: solid 1px $white;
      position: relative;
      position: absolute;
      left: 50%;
      transform: translate(-50%);
      @include transition;
      z-index: 1;
      &::after {
        content: "";
        position: absolute;
        width: 13px;
        height: 13px;
        background-color: $periwinkle;
        top: 50%;
        left: auto;
        right: 3px;
        transform: translateY(-55%);
        border-radius: 50%;
        @include transition;
      }
    }
    input#temadark[type="radio"]:checked ~ span {
      &::after {
        left: 3px;
        right: auto;
      }
    }
    input#temalight[type="radio"]:checked ~ span {
      &::after {
        left: auto;
        right: 3px;
      }
    }
  }
  &--tiers {
    @include flex-aligne-center;
    input[type="radio"]:checked + label {
      color: lighten(green, 25%);
      @include transition;
      padding: 0 0 0 0;
    }
    input[type="radio"]:not(:checked) + label {
      color: green;
      @include transition;
      padding: 0 0 0 0;
    }
    input[type="radio"]:checked + label:after,
    input[type="radio"]:checked + label::before,
    input[type="radio"]:not(:checked) + label:after,
    input[type="radio"]:not(:checked) + label::before {
      display: none;
    }
    span {
      font-size: 15px;
    }
    @include flex-aligne-center;
    column-gap: 5px;
  }

